<template>
    <el-container class="container">
        <el-header>
            <audio ref="musicPlayer" loop src="../assets/resouse/bg.mp3">您的浏览器不支持 audio 元素</audio>
            <button ref="musicPlayerBtn" :class="[ 'playerBtn' , playing ? 'el-icon-video-pause':'el-icon-video-play' ]" @click="togglePlay()"></button>
        </el-header>
        <el-main >
            <div class="cellBox">
                <div class="cell" v-for="(fit,index) in fits" :key="index" @click="goto(fit.path)" @mouseenter="mouseOver()" ref="cell">
                    <el-image :src="fit.url" fit="contain"></el-image>
                </div>
            </div>
        </el-main>
        <el-footer >哟，来了您呐~</el-footer>
    </el-container>
</template>
<script>
import { mouseMic } from '../assets/js/mic'
export default {
    data(){
        return{
            //音乐播放/暂停
            playing : false ,
            fits: [
                {url : require('../assets/resouse/cell.jpg'), path : '/opHome' },
                {url : require('../assets/resouse/cell.jpg'), path : '/aa' },
                {url : require('../assets/resouse/cell.jpg'), path : '/aa' },
                {url : require('../assets/resouse/cell.jpg'), path : '/aa' },
                {url : require('../assets/resouse/cell.jpg'), path : '/aa' },
                {url : require('../assets/resouse/cell.jpg'), path : '/aa' },
            ],
        }
    },
    methods:{
        // 播放音乐
        togglePlay() {
            const audio = this.$refs.musicPlayer;
            if (audio.paused) {
                this.playing = true;
                audio.play();
            } else {
                this.playing = false;
                audio.pause();
            }
        },
        // 跳转路由
        goto(path){
            this.$router.push(path)
        },
        // 鼠标移入事件
        mouseOver(){
            mouseMic()
        },
         // 鼠标音效
    }
}
</script>

<style lang="less" scoped>
.container{
    margin: 0;
    padding: 0;
    height: 100%;
    background-image: url('../assets/resouse/bg.png'); 
    background-size: cover; 
    background-position: center;
    background-repeat: repeat; 
    .el-header{
        text-align: right;
        background-color: rgba(255, 255, 255, 0.3) ;
        padding: 1rem;
        border-radius: 0 0 10px 10px;
        .playerBtn{
            width: 2rem;
            height: 2rem;
            font-size: 2rem;
            border-radius: 1rem;
            cursor: pointer;
            border: none;
            background-color: rgba(249, 248, 250, 0);
            color: rgb(255, 255, 255,0.2);
        }
        .playerBtn:hover {
            color: rgba(255, 255, 255, 0.6);
        }
    }
    .el-main{
        display: flex;
        justify-content:center;
        width: 100%;
        // margin: 6rem auto 0;
        // overflow: hidden;
        .cellBox{
            display: -webkit-flex;
            display: flex;
            flex-direction:row;
            flex-wrap:wrap;
            justify-content:center;
            width: 34rem;
            height: 22rem;
            padding: 2rem;
            margin-top: 6rem;
            background-color: rgba(255, 255, 255, 0.3);
            .cell{
                width: 10rem;
                height: 10rem;
                /* border: solid; */
                background-color: rgba(249, 248, 250, 0.2);
                margin: 0.5rem;
                position: relative;
                .el-image{
                    width: 9rem;
                    height: 9rem;
                    margin: 0.5rem 0.5rem;
                    border-radius: 1rem
                }
            }
        }
    }
    

    .el-footer{
        background-color: rgba(255, 255, 255, 0.3) ;
        height: 30px !important;
        text-align: center;
        color: #fff;
    }
    

}
</style>